import React, {PureComponent} from 'react'
import {Form, Button, Row, Col, Input, message} from 'antd'
import style from './style/Form.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {debounce} from 'lodash'

@connect((state) => ({...state.reservoirAreaConfig}), mapDispatchToProps)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  // 改变当前的输入值
  onInputChange(values) {
    const {searchParams, update} = this.props
    update({searchParams: {...searchParams, ...values}})
  }
  // 新增
  handleAdd = () => {
    const {
      searchParams: {sysValue},
      handleAddOrEdit
    } = this.props
    if (!sysValue) {
      message.error('请输入名称')
      return false
    }
    if (sysValue.length > 10) {
      message.error('库区名称长度不能超过10位')
      return false
    }
    handleAddOrEdit({data: {sysValue}}, () => {
      message.success('新增成功')
      const {handleGetList, update} = this.props
      update({searchParams: {}})
      handleGetList()
    })
  }
  render() {
    const {
      searchParams: {sysValue}
    } = this.props

    return (
      <Form className={style.form}>
        <Row type="flex" justify="start">
          <Col span={6}>
            <div className="commonInput">
              <label>库区名称：</label>
              <div className="box">
                <Input
                  value={sysValue}
                  placeholder="输入名称(长度不超过10位)"
                  onChange={(e) => {
                    const val = e.target.value
                    console.log('val is', val)
                    if (val.length > 10) {
                      return false
                    }
                    this.onInputChange({sysValue: e.target.value})
                  }}
                />
              </div>
            </div>
          </Col>
          <Col>
            <Button
              type="primary"
              style={{marginLeft: '16px'}}
              onClick={this.handleAdd}
            >
              新增
            </Button>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



